.TodoList {
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
  grid-gap: 24px;
}

.TodoList__item {
  position: relative;
  display: flex;
  align-items: center;
  padding: 20px;
  border: 1px solid black;
  border-radius: 4px;
}

.TodoList__text {
  margin-top: 0;
  margin-bottom: 0;
  margin-right: 24px;
  margin-left: 24px;
}

.TodoList__checkbox {
  cursor: pointer;
  width: 30px;
  height: 30px;
}

.TodoList__btn {
  margin-left: auto;
  border-radius: 4px;
  padding: 12px 24px;
  background-color: indigo;
  color: white;
  font-weight: 500;
}

.TodoList__btn:hover,
.TodoList__btn:focus {
  background-color: indigo;
}

.TodoList__btn:active {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
}
